.about-sec {
  font-family: "Inter", sans-serif;
  margin-top: 10rem;
  .title {
    text-align: center;
    margin-bottom: 2rem;
    h3 {
      font-weight: 400;
      text-transform: capitalize;
    }
    h1 {
      text-transform: uppercase;
      font-size: 3rem;
    }
  }

  .services {
    margin: 1rem 8rem;
    .grid-item {
      padding: 1rem;
      .card {
        border-radius: 5px;
        box-shadow: rgba(149, 157, 165, 0.1) 0px 8px 30px;
        padding: 3rem 2.5rem;
        text-align: center;
        border: solid 1px #bea6ff;
        img {
          width: 20%;
        }

        .title {
          font-weight: 600;
          font-size: 1.4rem;
          margin: 1rem 0;
        }
        .desc {
          font-family: "Roboto", sans-serif;
          font-weight: 300;
        }

        &.center {
          background-color: #946dff;
          color: #ffffff;
        }
      }
    }
  }
}

@media all and (max-width: 1200px) {
  .about-sec {
    font-family: "Inter", sans-serif;
    margin-top: 10rem;
    .title {
      text-align: center;
      margin-bottom: 2rem;
      h3 {
        font-weight: 400;
        text-transform: capitalize;
      }
      h1 {
        text-transform: uppercase;
        font-size: 3rem;
      }
    }

    .services {
      margin: 1rem 1rem;
      .grid-item {
        padding: 1rem;
        .card {
          border-radius: 5px;
          box-shadow: rgba(149, 157, 165, 0.1) 0px 8px 30px;
          padding: 3rem 2.5rem;
          text-align: center;
          border: solid 1px #bea6ff;
          img {
            width: 20%;
          }

          .title {
            font-weight: 600;
            font-size: 1.4rem;
            margin: 1rem 0;
          }
          .desc {
            font-family: "Roboto", sans-serif;
            font-weight: 300;
          }

          &.center {
            background-color: #946dff;
            color: #ffffff;
          }
        }
      }
    }
  }
}
